import './About.css'
const About = (props) => {
    const Log = () =>{
        window.open('https://drive.google.com/file/d/1aFkk8gYmlTuay7PjRU0Z_6oY4s1xbroM/view?usp=sharing',"_blank");
    }
    return(
        <div className='Right'>
            <h1 className='Heading'>About</h1>
            <hr />
            <button className="resume-download" onClick={Log}>Download Resume</button>
            <div className="AboutMe">
                <h2 className="Sub-Heading">About Me</h2>
                <p className="Ame">I am pre-final year undergraduate studying computer science and engineering at Dr.SPM International Institute of Information Technology - IIIT Naya Raipur. I am a motivated, enthusiastic and hardworking learner currently working in the field of Computer Vision.</p>
                <p className="Ame">My Primary Field of interest is Computer Vision and Deep Learning. My expertise includes Conputer Vision and Neural Network Architectures. Other field of interests include Web Development. </p>
            </div>
            <div className="Skillss">
                <h2 className='Sub-Heading'>Skills</h2>
                    <ul className="Skills">
                        <li className="Skill">Java</li>
                        <li className="Skill">Machine Learning</li>
                        <li className="Skill">HTML</li>
                        <li className="Skill">CSS</li>
                        <li className="Skill">JavaScript</li>
                        <li className="Skill">ReactJS</li>
                        <li className="Skill">Python</li>
                        <li className="Skill">MySQL</li>
                        <li className="Skill">Shell Scripting</li>
                        <li className="Skill">Deep Neural Networks</li>
                        <li className="Skill">Computer Vision</li>
                        <li className="Skill">Data Science</li>
                        <li className="Skill">Data Structures</li>
                        <li className="Skill">TensorFlow</li>
                        <li className="Skill">Linux</li>
                        <li className="Skill">MS Office</li>
                        <li className="Skill">Internet Of Things(IoT)</li>
                        <li className="Skill">Problem Solving</li>
                        <li className="Skill">Leadership</li>
                        <li className="Skill">Project Management</li>
                    </ul>
            </div>
            
            <br />
            <br />
            <br />

        </div>
    );
}
export default About;